@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&display=swap);
/** @format */

:root {
	--btn-background: hsl(180, 66%, 49%);
	--small-text-color: hsl(0, 0%, 75%);
	--grayish: hsl(225, 33%, 95%);
	--dark-violet: hsl(257, 27%, 26%);
	--very-dark-violet: hsl(260, 8%, 14%);
}
/* ### Primary

### Secondary

- Red: hsl(0, 87%, 67%)

### Neutral

- Gray: hsl(0, 0%, 75%)
- Grayish Violet: hsl(257, 7%, 63%)
- Very Dark Blue: hsl(255, 11%, 22%)
- Very Dark Violet: hsl(260, 8%, 14%)

## Typography

### Body Copy

- Font size: 18px */
html,
body {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	font-size: 16px;
	font-family: 'Poppins', sans-serif;
}
.App-container {
	min-width: 100%;
	box-sizing: border-box;

	overflow-x: hidden;
}
.App-header {
	min-width: 100%;
}

/** @format */

.footer-container {
	background-color: var(--very-dark-violet);
	color: #fff;
	padding-bottom: 2rem;
}
.footer-sub-container {
	display: flex;
	align-items: flex-start;
	justify-content: space-between;
	width: 80%;
	margin: auto;
	padding-top: 4rem;
	/* border: 2px solid red; */
}
.logo {
	fill: #fff;
}

.features,
.resources,
.company {
	display: flex;
	flex-direction: column;
}
.links ul {
	padding-left: 0;
}
.links ul > li {
	list-style: none;
}
.links b,
.links li {
	cursor: pointer;
	padding: 0.4rem;
}
.links li {
	color: var(--small-text-color);
}
.links li:hover {
	color: var(--btn-background);
}
.icon {
	margin-left: 1rem;
	cursor: pointer;
	fill: #fff;
}
.icon:hover {
	fill: var(red);
}

@media screen and (max-width: 600px) {
	.footer-sub-container {
		flex-direction: column;
		align-items: center;
		text-align: center;
	}
	.logo {
		margin-bottom: 2rem;
		fill: #fff;
	}
	.icon:hover {
		fill: var(--btn-background);
	}
}

/** @format */
.hero-page {
	max-width: 80%;
	margin: 3rem auto 6rem auto;
	display: flex;
}
.vector-image {
	width: 600px;
	-webkit-transform: translateX(35%);
	        transform: translateX(35%);
}
.hero-page-title {
	font-size: 3.5rem;
	margin-bottom: 0;
}
.hero-page-tagline {
	margin-top: 0;
	/* width: 90%; */
}
.hero-page-btn {
	background-color: var(--btn-background);
	color: #fff;
	border: none;
	border-radius: 50px;
	padding: 0.5rem 1rem;
	width: 150px;
	font-weight: 700;

	cursor: pointer;
}
@media only screen and (max-width: 600px) {
	.hero-page {
		flex-direction: column;
	}
	.vector-image {
		order: -1;
		-webkit-transform: translateX(0%);
		        transform: translateX(0%);
	}
	.hero-page-title {
		font-size: 2.5rem;
		text-align: center;
	}
	.hero-page-tagline {
		text-align: center;
		word-wrap: normal;
	}
	.hero-page-btn {
		width: 50%;
		display: block;
		margin: 1rem auto;
	}
}

/** @format */

.Nav-container {
	width: 80%;
	margin: auto;
	display: flex;
	align-items: center;
	justify-content: space-around;
}

.Nav-list {
	flex: 1 1;
	display: flex;
	justify-content: space-between;
	align-items: center;
	list-style: none;
}
.Nav-container li {
	cursor: pointer;
	color: var(--small-text-color);
}
.login-container {
	justify-self: flex-end;
	flex-basis: 70%;
	display: flex;
	justify-content: flex-end;
	align-items: center;
}

.login {
	margin-right: 2rem;
}
.signup {
	background-color: var(--btn-background);
	color: #fff;
	padding: 0.5rem 1rem;
	border-radius: 50px;
	font-weight: 700;
	display: block;
	border: none;
	cursor: pointer;
}
@media only screen and (max-width: 600px) {
	.Nav-container {
		margin-top: 2.5rem;
		width: 90%;
		justify-content: space-between;
		align-items: center;
	}
	.Nav-list {
		z-index: 10;
		border-radius: 10px;
		width: 80%;
		position: absolute;
		left: 10% !important;
		top: 12% !important;
		right: 34.3431% !important;
		padding-left: 0;
		flex-direction: column;
		background-color: var(--dark-violet);
	}
	.Nav-list li {
		width: 100%;
		text-align: center;
		margin: 1rem;
	}
	.nav-lis-show {
		visibility: visible;
	}
	.nav-list-hide {
		visibility: hidden;
	}
	.login-container {
		width: 100%;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
	.login {
		text-align: center;
		margin-left: auto;
		margin: 1rem;
	}
	.signup {
		margin: 1rem;
		width: 90%;
	}

	.menu {
		height: 20px;
		align-self: flex-start;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		cursor: pointer;
	}
	.menu div {
		border-radius: 5px;
		background-color: var(--dark-violet);
		width: 25px;
		height: 3px;
	}
}

/** @format */

.statistics-page {
	background-color: var(--grayish);
}
.advanced-statistics-title {
	text-align: center;
}

.advanced-statistics-para {
	text-align: center;
	width: 35%;
	margin: auto;
}
.chart {
	width: 80%;
	margin: 3rem auto;
	font-size: 14px;
	overflow-y: visible;
	display: flex;
	justify-content: space-between;
}
.chart-item {
	padding: 1rem;
	border-radius: 5px;
	width: 300px;
	background-color: #fff;
}
.chart-item img {
	border-radius: 50px;
	padding: 1rem;
	background-color: var(--dark-violet);
	width: 30px;
	-webkit-transform: translateY(-80%);
	        transform: translateY(-80%);
}
.record {
	-webkit-transform: translateY(10%);
	        transform: translateY(10%);
}
.customizable {
	-webkit-transform: translateY(20%);
	        transform: translateY(20%);
}
.link-boost {
	background-image: url(/url-shorten-api/static/media/bg-boost-desktop.772154c9.svg);
	margin-top: 10rem;
	padding: 2rem;
	background-color: var(--dark-violet);
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	color: #fff;
}
/* --------UrlShorten Page ----------- 
*/
.url-shorten {
	width: 80%;
	margin: auto;
	background-color: var(--dark-violet);
	background-image: url(/url-shorten-api/static/media/bg-boost-desktop.772154c9.svg);
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	display: flex;
	justify-content: center;
	border-radius: 10px;
}
.url-shorten input {
	width: 70%;
	padding: 0.5rem;
	margin: 2rem;
}
.url-shorten button {
	margin: 2rem;
	display: block;
	justify-self: flex-start;
	padding: 0.5rem 2rem;
	background-color: var(--btn-background);
	color: #fff;
	font-weight: 700;
	cursor: pointer;
	border: none;
	border-radius: 5px;
}
.link-boost-btn {
	background-color: var(--btn-background);
	color: #fff;
	border: none;
	border-radius: 50px;
	padding: 0.7rem 2rem;
	width: 15%;
	cursor: pointer;
}
@media screen and (max-width: 600px) {
	.url-shorten {
		flex-direction: column;
		box-sizing: border-box;
	}
	.url-shorten input {
		margin: 0;
		width: 80%;
		margin: 1rem auto;
	}
	.url-shorten button {
		width: 86%;
		margin: 1rem auto;
	}

	.advanced-statistics-info {
		width: 80%;
		margin: auto;
	}
	.advanced-statistics-title {
		font-size: 1.7rem;
	}
	.advanced-statistics-para {
		width: 100%;
	}
	.chart {
		flex-direction: column;
		align-items: center;
	}
	.chart-item {
		margin-bottom: 2rem;
		text-align: center;
	}

	.link-boost-btn {
		margin: 2rem auto;
		width: 80%;
	}
}

